
@media only screen and (-webkit-device-pixel-ratio: 2) {
.border-1px::before {
    -webkit-transform: scaleY(0.5);
            transform: scaleY(0.5);
}
}
@media only screen and (-webkit-device-pixel-ratio: 3) {
.border-1px::before {
    -webkit-transform: scaleY(0.333333);
            transform: scaleY(0.333333);
}
}
.search {
  width: 100%;
}
.search .search_form {
  *zoom: 1;
  margin-top: 45px;
  background-color: #fff;
  padding: 12px 8px;
}
.search .search_form::after {
  content: '';
  display: block;
  clear: both;
}
.search .search_form input {
  height: 35px;
  padding: 0 4px;
  border-radius: 2px;
  font-weight: bold;
  outline: none;
}
.search .search_form input.search_input {
  float: left;
  width: 79%;
  border: 4px solid #f2f2f2;
  font-size: 14px;
  color: #333;
  background-color: #f2f2f2;
}
.search .search_form input.search_submit {
  float: right;
  width: 18%;
  border: 4px solid #02a774;
  font-size: 16px;
  color: #fff;
  background-color: #02a774;
}
.search .list .list_container {
  background-color: #fff;
}
.search .list .list_container .list_li {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  padding: 10px;
  border-bottom: 1px solid #e4e4e4;
}
.search .list .list_container .list_li .item_left {
  margin-right: 10px;
}
.search .list .list_container .list_li .item_left .restaurant_img {
  width: 50px;
  height: 50px;
  display: block;
}
.search .list .list_container .list_li .item_right {
  font-size: 12px;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.search .list .list_container .list_li .item_right .item_right_text,
.search .list .list_container .list_li .item_right p {
  line-height: 12px;
  margin-bottom: 6px;
}
.search .list .list_container .list_li .item_right .item_right_text:last-child,
.search .list .list_container .list_li .item_right p:last-child {
  margin-bottom: 0;
}
.search .search_none {
  margin: 0 auto;
  color: #333;
  background-color: #fff;
  text-align: center;
  margin-top: 0.125rem;
}


